header {
    border-bottom: solid 1px #ca2100;
    background-color: $generic-bg;
    -webkit-transition: height 200ms ease-in;
    transition: height 200ms ease-in;
    -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
    height: 44px;
    z-index: 400;
    position: fixed;
    top: 0;
    width: 100%;

    @include mq("900px") {
        padding: 0 10px;
    }

    nav {
        display: grid;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr;
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        max-width: 922px;
        margin: auto;
        height: 44px;

        p,
        li {
            color: white;
        }

        div {
            &.logo {
                padding-left: 8px;
                grid-area: 1/1;
            }

            img {
                width: 24px;
            }

            &.settings {
                grid-area: 1/2;
                text-align: right;

                ul {
                    float: right;

                    li {
                        @include va;

                        list-style-type: none;
                        cursor: pointer;
                        width: 30px;
                        height: 30px;
                        text-align: center;
                        vertical-align: middle;
                        float: left;

                        &:hover {
                            border-radius: 3px;
                            background: rgba(255, 255, 255, 0.2);
                        }

                        &.settings__add {
                            margin-right: 15px;
                            font-size: 30px;

                            button {
                                @include transparent-btn();
                                color: white;
                                cursor: pointer;
                            }
                        }

                        &.settings__darkmode {
                            button {
                                @include transparent-btn();
                                color: white;
                                cursor: pointer;
                                svg {
                                    width: 20px;
                                    height: 20px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
