.sidebar {
    @include no-select();
    width: 266px;
    height: calc(100vh);
    padding-top: 74px;
    position: fixed;
    overflow-x: hidden;
    overflow-y: hidden;
    border-right: $generic-border;
    background: #fafafa;

    @include mq("900px") {
        display: none;
    }

    li {
        &:hover .sidebar__project-delete {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
        }
    }

    &__project {
        cursor: pointer;
        
        .project-wrapper {
            padding: 5px 0;
            padding-left: 10px;
            padding-right: 15px;
            width: 100%;
            display: flex;
            align-items: center;
        }

        &-delete {
            margin-left: auto;
            display: none;

            svg {
                color: #cacaca;
            }
        }

        &.active,
        &:hover {
            font-weight: bold;
            background: white;
        }

        &:nth-child(1) {
            .sidebar__dot {
                color: #6accbc;
            }
        }
        &:nth-child(2) {
            .sidebar__dot {
                color: #fad003;
            }
        }
        &:nth-child(3) {
            .sidebar__dot {
                color: #ff8d85;
            }
        }
        &:nth-child(4) {
            .sidebar__dot {
                color: #ff9932;
            }
        }
        &:nth-child(5) {
            .sidebar__dot {
                color: #af38eb;
            }
        }
    }

    &__projects {
        padding-left: 3px;
    }

    &__project {
        @include va("left");
    }

    &__dot {
        margin-right: 10px;
        font-size: 30px;
    }

    &__middle {
        margin-top: 20px;
        margin-bottom: 20px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        border-bottom: $generic-border;
        padding-left: 10px;
        cursor: pointer;

        span {
            color: #0000008a;
            margin-right: 10px;

            svg.hidden-projects {
                -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
            }
        }

        h2 {
            margin: 0;
            color: #333;
            font-size: 15px;
            font-weight: bold;
            padding-bottom: 20px;
        }
    }

    &__generic {
        li {
            list-style-type: none;
            color: #333;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            cursor: pointer;
            line-height: 1.5;
            font-size: 15px;
            padding-left: 0;
            padding-right: 0;

            div:nth-child(1) {
                display: flex;
                padding: 10px 0 10px 10px;
                width: 100%;

                span:first-of-type {
                    margin-right: 10px;
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    -webkit-box-align: center;
                    -ms-flex-align: center;
                    align-items: center;

                    svg {
                        width: 18px;
                        height: 18px;
                        color: #555;
                    }
                }
            }

            &.active,
            &:hover {
                font-weight: bold;
                background: white;
            }
        }
    }

    
}
